body {
  background-color: #fefefe;
  padding: 0;
}

header {
  color: palegoldenrod;
  font-size: 17px;
  display: flex;
  flex-flow: row;
  padding: 10px 0;
  padding-top: 15px
}

.me {
  color: goldenrod;
}

header h3 {
  flex: 1;
  margin: 0;
  display: block;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.hdr-dots {
  text-align: right;
}

.hdr-dot-red {
  display: inline-block;
  height: 20px;
  width: 20px;
  background-color: red;
  border-radius: 50%;
}

.App {
  font-size: 15px;
  margin: 20px auto;
  width: auto;
  max-width: 700px;
  padding: 10px 30px;
  padding-bottom: 30px;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 1px #bbb;
  border-radius: 3px;
  color: #fefefe;
  background-color: #2e2e2e;
}

@media screen and (max-width: 760px) {
  .App {
    margin: 0 auto;
  }
}

.footer {
  margin: 30px auto;
  width: auto;
  max-width: 700px;
  padding: 10px 30px;
  text-align: right;
}

.redtxt {
  color: red;
}

hr {
  margin: 25px 0;
  border-color: #555;
  border-width: 0.5px;
}

.cursor {
  animation: blink 0.8s infinite;
  font-weight: bold;
}

@keyframes blink {
  0% {
    opacity: 1
  }

  50% {
    opacity: 0
  }

  100% {
    opacity: 1
  }
}

.heart {
  animation: blink2 0.5s infinite;
  font-size: 17px;
}

@keyframes blink2 {
  0% {
    color: red
  }

  50% {
    color: yellow
  }

  100% {
    color: red
  }
}

.code {
  display: flex;
  flex-flow: row;
}

.code code:last-child {
  flex: 1;
}

.code code:first-child {
  color: #888;
}

.code a {
  color: lightblue;
}

.code svg {
  margin-right: 10px;
}

.ccc {
  color: #222;
  background-color: #ccc;
}